import React, { useState, useEffect } from 'react';
import { Table } from 'antd';
import { numberToMoney } from 'utils/formatUtil';
import { getHistoricalReceiptsByStudentId } from 'services/student';
import moment from 'moment';

const createSubReceipt = receiptDetails =>
  receiptDetails.map((receiptDetail, index) => ({
    key: index.toString(),
    name: receiptDetail.itemName,
    amount: receiptDetail.amount,
  }));

const createReceipt = receipt => {
  const { yearEdu, semester, paymentDate, paymentContent, amount } = receipt;
  return {
    yearEdu,
    semester,
    paymentDate: moment(paymentDate).format('HH:mm:ss DD/MM/YYYY'),
    name: paymentContent,
    amount,
  };
};

const getHistoricalReceipts = async (id, setData) => {
  const res = await getHistoricalReceiptsByStudentId(id);
  const result = res.data.items.map((receipt, index) => {
    const receiptItemDetails = createSubReceipt(receipt.details);
    const receiptItem = createReceipt(receipt);
    receiptItem.key = index.toString();
    receiptItem.receiptDetails = receiptItemDetails;
    return receiptItem;
  });
  setData(result);
};

const HistoricalReceipt = ({ student }) => {
  const [data, setData] = useState([]);

  const columns = [
    { title: 'Tên hóa đơn', dataIndex: 'name', key: 'name' },
    {
      title: 'Số tiền',
      dataIndex: 'amount',
      key: 'amount',
      render: text => `${numberToMoney(text)}₫`,
    },
    { title: 'Học kì', dataIndex: 'semester', key: 'semester' },
    { title: 'Năm', dataIndex: 'yearEdu', key: 'yearEdu' },
    { title: 'Ngày đóng', dataIndex: 'paymentDate', key: 'paymentDate' },
  ];

  const expandedRowRender = record => {
    const receiptDetailColumns = [
      { title: 'Tên chi tiết', dataIndex: 'name', key: 'name' },
      {
        title: 'Số tiền',
        dataIndex: 'amount',
        key: 'amount',
        render: text => `${numberToMoney(text)}₫`,
      },
    ];

    return (
      <Table
        columns={receiptDetailColumns}
        dataSource={record.receiptDetails}
        pagination={false}
        rowKey="key"
      />
    );
  };

  useEffect(() => {
    const { id } = student;
    getHistoricalReceipts(id, setData);
  }, []);

  return (
    <Table
      columns={columns}
      expandedRowRender={record => expandedRowRender(record)}
      dataSource={data}
      pagination={false}
      rowKey="key"
    />
  );
};

export default HistoricalReceipt;



