import React, { useState, useEffect } from 'react';
import { Table } from 'antd';
import { getTrainingScoresByStudentId } from 'services/student';

const columns = [
  {
    align: 'center',
    title: 'Năm học',
    dataIndex: 'yearEdu',
    key: 'yearEdu',
    width: '20%',
  },
  { align: 'center', title: 'Học kì', dataIndex: 'semester', key: 'semester', width: '20%' },
  {
    title: 'Tiêu chí đánh giá',
    dataIndex: 'objective',
    key: 'objective',
    width: '30%',
  },
  { align: 'center', title: 'Điểm', dataIndex: 'point', key: 'point', width: '15%' },
  {
    align: 'center',
    title: 'Số ngày CTXH',
    dataIndex: 'noOfSocialDay',
    key: 'noOfSocialDay',
    width: '15%',
  },
];

const getTrainingScores = async (id, setData) => {
  const { data } = await getTrainingScoresByStudentId(id);
  const yearsInfo = data.items.map(yearItem => {
    const yearInfo = {};
    yearInfo.key = yearItem.yearEduLabel;
    yearInfo.yearEdu = yearItem.yearEduLabel;
    yearInfo.children = yearItem.semesterInfos.map(semesterItem => {
      const semesterInfo = {};
      semesterInfo.key = `${yearInfo.key}_${semesterItem.semesterLabel}`;
      semesterInfo.semester = semesterItem.semesterLabel;
      semesterInfo.children = semesterItem.trainingScoreInfo.map(trainingCoreItem => {
        const children = {
          key: `${semesterInfo.key}_${trainingCoreItem.criteriaTypeLabel}`,
          objective: trainingCoreItem.criteriaTypeLabel,
          point: trainingCoreItem.score,
          noOfSocialDay: trainingCoreItem.noOfSocialDay,
        };
        return children;
      });
      return semesterInfo;
    });
    return yearInfo;
  });
  setData(yearsInfo);
};

const TrainingScore = ({ student }) => {
  const [data, setData] = useState([]);
  useEffect(() => {
    const { id } = student;
    getTrainingScores(id, setData);
  }, []);
  return <Table columns={columns} dataSource={data} pagination={false} />;
};

export default TrainingScore;



