import React from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Tabs, Form, Input, message } from 'antd';
import { getStudentById, updateStudent } from 'services/student';
import { getCandidateById } from 'services/studentCandidate';
import moment from 'moment';
import { BtnSave, BtnCancel } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';

import './style.scss';
import FormInfoBasic from './FormInfoBasic';
import FormInfoBasic1 from './FormInfoBasic_1';
import FormInfoAddrBasic from './FormInfoAddrBasic';
import FormInfoTS from './FormInfoTS';
import AvatarUpload from './AvatarUpload';
import TrainingScore from './TrainingScore';
import HistoricalReceipt from './HistoricalReceipt';
import StudyScore from './StudyScore';
import Family from './Family';

const { TabPane } = Tabs;
@Form.create({ name: 'ProfileStudent' })
class ProfileApp extends React.Component {
  state = {
    background: '',
    student: {},
  };

  componentDidMount = async () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      const { data: student } = await getStudentById({ id: queryParamId });
      const { data: candidate } = await getCandidateById({ id: student.candidateId });
      student.candidateData = candidate;
      this.initValueForm(student);
      this.setState({ student });
    }
  };

  initValueForm = student => {
    const { setFieldsValue } = this.props.form;
    student.dateIssue = moment(student.dateIssue);
    // student.unionDate = moment(student.unionDate);
    // student.politicalDate = moment(student.politicalDate);
    setFieldsValue(student);
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      console.log('summit too', values);
      const { student } = this.state;
      const { id, candidateId } = student;
      const {
        firstname,
        lastname,
        code,
        idCard,
        placeIssue,
        dateIssue,
        pobId,
        dob,
        gender,
        homeVillage,
        nationId,
        ethnicityId,
        religion,
        comeFromId,
        unionDate,
        politicalDate,
        permanentAddress,
        wardId,
        townId,
        districtId,
        policyObjectId,
        subsidizeObjectId,
        prioritizeObjectId,
        homePhone,
        phone,
        email,
        mailAddress,
        emergencyAddress,
        estimatedNote,
        foreignLanguage,
        liveAddress,
      } = values;
      await updateStudent({
        id,
        candidateId,
        code,
        firstName: firstname,
        lastName: lastname,
        idCard,
        placeIssue,
        dateIssue,
        pobId,
        dob,
        gender,
        homeVillage,
        nationId,
        ethnicityId,
        religion,
        comeFromId,
        unionDate,
        politicalDate,
        permanentAddress,
        wardId,
        townId,
        districtId,
        policyObjectId,
        subsidizeObjectId,
        prioritizeObjectId,
        homePhone,
        phone,
        email,
        mailAddress,
        emergencyAddress,
        estimatedNote,
        foreignLanguage,
        liveAddress,
      });
      message.success('Lưu thành công');
    });
  };

  render() {
    const { background, student } = this.state;
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit}>
        <div className="profile">
          <div className="row">
            <div style={{ paddingRight: 5 }} className="col-xl-4">
              <div
                className="card profile__header"
                style={{ backgroundImage: `url(${background})` }}
              >
                <div className="profile-student__header-card">
                  <div className="card-body text-center">
                    <AvatarUpload
                      profileId={student.id}
                      imgStorageId={student.avatarFileId}
                      updateAvatarFile={this.updateAvatarFile}
                      // isReset={!isPopupClose}
                    />
                    <br />
                  </div>
                </div>
                <div className="profile__header-card">
                  <div className="card-body text-center">
                    <Form className="form-inline-label" layout="vertical">
                      <Form.Item label="Họ">
                        {getFieldDecorator('firstname', {})(<Input />)}
                      </Form.Item>
                      <Form.Item label="Tên">
                        {getFieldDecorator('lastname', {})(<Input />)}
                      </Form.Item>
                      <Form.Item label="MSSV">{getFieldDecorator('code', {})(<Input />)}</Form.Item>
                    </Form>
                  </div>
                </div>
              </div>
              <div className="card">
                <div className="card-body">
                  <div className="profile__actions">
                    <FormInfoBasic1 {...this.props} student={student} />
                  </div>
                </div>
              </div>
              <div className="card">
                <div className="card-body">
                  <h5 className="mb-3 text-black">
                    <strong>Thông tin tuyển sinh</strong>
                  </h5>
                  <div className="profile__actions">
                    <FormInfoTS {...this.props} />
                  </div>
                </div>
              </div>
            </div>
            <div style={{ paddingLeft: 5 }} className="col-xl-8">
              <div className="card">
                <div className="card-body">
                  <Tabs defaultActiveKey="1">
                    <TabPane
                      tab={
                        <span>
                          <i className="icmn-info" /> Thông tin
                        </span>
                      }
                      key="1"
                    >
                      <div className="">
                        <div className="card">
                          <div className="card-body">
                            <h5 className="mb-3 text-black">
                              <strong>Thông tin lý lịch</strong>
                            </h5>
                            <div className="profile__actions">
                              <FormInfoBasic {...this.props} student={student} />
                            </div>
                          </div>
                        </div>
                        <div className="card">
                          <div className="card-body">
                            <h5 className="mb-3 text-black">
                              <strong>Thông tin nơi chốn</strong>
                            </h5>
                            <div className="profile__actions">
                              <FormInfoAddrBasic {...this.props} />
                            </div>
                          </div>
                        </div>
                        <div style={{ textAlign: 'center' }}>
                          <BtnSave htmlType="submit" />
                          <BtnCancel onClick={() => this.props.history.goBack()} />
                        </div>
                      </div>
                      <hr />
                    </TabPane>
                    <TabPane
                      tab={
                        <span>
                          <i className="icmn-address-book" /> Thông tin gia đình
                        </span>
                      }
                      key="5"
                    >
                      <Family studentId={student.id} />
                    </TabPane>
                    <TabPane
                      disabled={Object.keys(student).length === 0}
                      tab={
                        <span>
                          <i className="icmn-pie-chart" /> Điểm rèn luyện
                        </span>
                      }
                      key="2"
                    >
                      <TrainingScore student={student} />
                    </TabPane>
                    <TabPane
                      disabled={Object.keys(student).length === 0}
                      tab={
                        <span>
                          <i className="icmn-history" /> Lịch sử đóng học phí
                        </span>
                      }
                      key="3"
                    >
                      <HistoricalReceipt student={student} />
                    </TabPane>
                    <TabPane
                      disabled
                      tab={
                        <span>
                          <i className="icmn-address-book" /> Điểm học tập
                        </span>
                      }
                      key="4"
                    >
                      <StudyScore />
                    </TabPane>
                  </Tabs>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Form>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ProfileApp {...props} />
  </Page>
));



