import React, { useState, useEffect } from 'react';
import { Form, Row, Input, Select, message } from 'antd';
import { DatePickerString } from 'components/common';
import { BtnSave, BtnClear } from 'components/common/button';
import { getRelationshipTypes, createRelationship } from 'services/student';

const { Option } = Select;

const CreateForm = props => {
  const [types, setTypes] = useState([]);
  const { getFieldDecorator } = props.form;

  const handleReset = () => {
    props.form.resetFields();
  };

  const processCreateRelationship = async values => {
    try {
      const { studentId } = props;
      await createRelationship({ ...values, studentId });
      message.success('Thêm thành viên mới thành công');
    } catch (ex) {
      message.error('Thêm thành viên mới thất bại');
    } finally {
      props.hideModal();
    }
  };

  const handleSearch = () => {
    props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      processCreateRelationship(values);
    });
  };

  const getRelationshipTypeSelects = async () => {
    const res = await getRelationshipTypes();
    setTypes(res.data.items);
  };

  useEffect(() => {
    getRelationshipTypeSelects();
  }, []);

  return (
    <Form className="form-inline-label" layout="vertical">
      <Row gutter={24}>
        <Form.Item label="Họ và tên">
          {getFieldDecorator('fullname', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<Input />)}
        </Form.Item>
      </Row>
      <Row gutter={24}>
        <Form.Item label="Ngày sinh">
          {getFieldDecorator('dob', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<DatePickerString placeholder="Chọn ngày sinh" />)}
        </Form.Item>
      </Row>
      <Row gutter={24}>
        <Form.Item label="Địa chỉ">
          {getFieldDecorator('homeAddress', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<Input />)}
        </Form.Item>
      </Row>
      <Row gutter={24}>
        <Form.Item label="Công việc">
          {getFieldDecorator('career', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<Input />)}
        </Form.Item>
      </Row>
      <Row gutter={24}>
        <Form.Item label="Mối quan hệ">
          {getFieldDecorator('relationshipTypeId', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(
            <Select placeholder="Vui lòng chọn mối quan hệ">
              {types.map(relationshipType => (
                <Option value={relationshipType.value}>{relationshipType.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
      </Row>
      <Row gutter={24}>
        <Form.Item label="SĐT">
          {getFieldDecorator('phoneNo', {
            rules: [
              {
                required: true,
                message: 'Vui lòng nhập thông tin!',
              },
            ],
          })(<Input />)}
        </Form.Item>
      </Row>
      <Row gutter={24} style={{ textAlign: 'center' }}>
        <div className="btn-group">
          <BtnSave onClick={handleSearch} />
          <BtnClear onClick={handleReset} />
        </div>
      </Row>
    </Form>
  );
};

export default Form.create()(CreateForm);



