import React, { Component } from 'react';
import { Col, Form, Input } from 'antd';
import { SelectAjax } from 'components/common';
import { getListTown } from 'services/town';
import { getListNation } from 'services/nation';
import { getDistricts } from 'services/district';
import { getComeFrom } from 'services/comeFrom';
import { getWards } from 'services/ward';

class FormInfoAddrBasic extends Component {
  state = {};

  render() {
    if (!this.props.form) return null;
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const townId = getFieldValue('townId');
    const districtId = getFieldValue('districtId');
    return (
      <Form className="form-inline-label" layout="vertical">
        <Col span={12}>
          <Form.Item label="Quê quán">{getFieldDecorator('homeVillage', {})(<Input />)}</Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Quốc tịch">
            {getFieldDecorator('nationId', {})(<SelectAjax service={getListNation} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="TP xuất thân">
            {getFieldDecorator('comeFromId', {})(
              <SelectAjax service={getComeFrom} typeValue="int" />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="ĐC thường trú">
            {getFieldDecorator('permanentAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Tỉnh/Tp">
            {getFieldDecorator('townId', {})(<SelectAjax service={getListTown} />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Quận/Huyện">
            {getFieldDecorator('districtId', {})(
              <SelectAjax
                setFieldsValue={setFieldsValue}
                service={getDistricts}
                params={{ townId }}
                allowGetData={!!townId}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Xã/Phường">
            {getFieldDecorator('wardId', {})(
              <SelectAjax
                setFieldsValue={setFieldsValue}
                service={getWards}
                params={{ districtId }}
                allowGetData={!!districtId}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Địa chỉ báo tin">
            {getFieldDecorator('emergencyAddress', {})(<Input />)}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="Nơi ở hiện nay">
            {getFieldDecorator('liveAddress', {})(<Input />)}
          </Form.Item>
        </Col>
      </Form>
    );
  }
}

export default FormInfoAddrBasic;



