import React, { useState, Fragment, useEffect } from 'react';
import { Card, Row, Col, Form, Input, Modal, Select, message } from 'antd';
import { TableDataEditRow, DatePickerString } from 'components/common';
import {
  getFamily,
  updateRelationship,
  getRelationshipTypes,
  deleteRelationship,
} from 'services/student';
import { BtnAddNew, BtnDelete } from 'components/common/button';
import CreateForm from './CreateForm';

const { Option } = Select;

const getColumns = types => {
  const columns = [
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('fullname', {
            initialValue: record.fullname,
          })(<Input />)}
        </Form.Item>
      ),
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('dob', {
            initialValue: record.dob,
          })(<DatePickerString placeholder="Chọn ngày sinh" />)}
        </Form.Item>
      ),
    },
    {
      title: 'Địa chỉ',
      dataIndex: 'homeAddress',
      key: 'homeAddress',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('homeAddress', {
            initialValue: record.homeAddress,
          })(<Input />)}
        </Form.Item>
      ),
    },
    {
      title: 'Công việc',
      dataIndex: 'career',
      key: 'career',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('career', {
            initialValue: record.career,
          })(<Input />)}
        </Form.Item>
      ),
    },
    {
      title: 'Mối quan hệ',
      dataIndex: 'relationshipTypeLabel',
      key: 'relationshipTypeLabel',
      editable: true,
      renderFormItemEdit: ({ form, record }) =>  (
        <Form.Item>
          {form.getFieldDecorator('relationshipTypeId', {
            initialValue: record.relationshipTypeId,
          })(
            <Select placeholder="Vui lòng chọn mối quan hệ">
              {types.map(relationshipType => (
                <Option value={relationshipType.value}>{relationshipType.label}</Option>
              ))}
            </Select>
          )}
        </Form.Item>
        ),
    },
    {
      title: 'SĐT',
      dataIndex: 'phoneNo',
      key: 'phoneNo',
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('phoneNo', {
            initialValue: record.phoneNo,
          })(<Input />)}
        </Form.Item>
      ),
    },
  ];
  return columns;
};

const Family = ({ studentId }) => {
  const [visible, setVisible] = useState(false);
  const [reload, setReload] = useState(0);
  const [types, setTypes] = useState([]);

  const showModal = () => {
    setVisible(true);
  };

  const handleOk = () => {
    setVisible(false);
  };

  const handleCancel = () => {
    setReload(reload + 1);
    setVisible(false);
  };

  const handleSaveRowData = async rowData => {
    try {
      await updateRelationship(rowData);
      setReload(reload + 1);
      return rowData;
    } catch (e) {
      return null;
    }
  };

  const handleDeleteRowData = async id => {
    try {
      await deleteRelationship(id);
      message.success('Xóa thành viên thành công');
    } catch (ex) {
      message.error('Xóa thành viên thất bại');
    } finally {
      setReload(reload + 1);
    }
  };

  const getRelationshipTypeSelects = async () => {
    const res = await getRelationshipTypes();
    setTypes(res.data.items);
  };

  useEffect(() => {
    getRelationshipTypeSelects();
  }, []);

  return (
    <Card>
      <BtnAddNew onClick={showModal}>Thêm thành viên</BtnAddNew>
      <Row>
        <Col span={24}>
          <TableDataEditRow
            reload={reload}
            paramSearch={studentId}
            service={getFamily}
            columns={getColumns(types)}
            handleSaveRowData={handleSaveRowData}
            customBtnAction={({ record, btnAction }) => (
              <Fragment>
                {btnAction}
                <BtnDelete onClick={() => handleDeleteRowData(record.id)}>{record.id}</BtnDelete>
              </Fragment>
            )}
            rowKey="id"
          />
        </Col>
      </Row>
      <Modal
        title="Thêm thành viên gia đình"
        visible={visible}
        onOk={handleOk}
        onCancel={handleCancel}
        footer={null}
      >
        <CreateForm studentId={studentId} hideModal={handleCancel} />
      </Modal>
    </Card>
  );
};

export default Family;



